import React, { Component } from 'react';

import Chip from '@material-ui/core/Chip';

export default class WarsComponent extends Component {
  render() {
    const {wars} = this.props;
    return wars.split("|").map((war, index) => {
      let label = [];

      for (let w of war) {
        if (w === 'S') {
          label.push(<img height={16} src="/skip.png" />);
        } else if (w === 'L') {
          label.push(<img height={16} src="/lost.png" />);
        } else if (w === 'W') {
          label.push(<img height={16} src="/won.png" />);
        }
      }

      if (war === "N") {
        label.push(<img height={16} src="/placeholder.png" />);
      }

      // <img className={classes.logo} height={20} alt={'logo'} src={require('../images/logo.png')} />
      return <Chip key={index} label={label}/>;
    });
  }
}



