import React, { Component } from 'react';

import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableBody from "@material-ui/core/TableBody";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";

import {withStyles} from "@material-ui/core/styles";

import DonationsComponent from "./DonationsComponent";
import WarsComponent from "./WarsComponent";

const styles = theme => ({

});

class MembersComponent extends Component {
  render() {
    const {classes, data} = this.props;

    return (
      <Paper className={classes.root}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>Name (Role)</TableCell>
              <TableCell>Tag</TableCell>
              <TableCell numeric>Trophies</TableCell>
              <TableCell>Member Since</TableCell>
              <TableCell>Last Battle</TableCell>
              <TableCell>Donation History</TableCell>
              <TableCell>War History</TableCell>
              <TableCell numeric>Donations Score (max 300)</TableCell>
              <TableCell numeric>War Score (max 300)</TableCell>
              <TableCell numeric>Total score</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map((m, i) => {
              return (
                <TableRow key={i}>
                  <TableCell>{m.name} ({m.role})</TableCell>
                  <TableCell>{m.tag}</TableCell>
                  <TableCell numeric>{m.trophies}</TableCell>
                  <TableCell>{new Date(m.memberSince * 1000).toLocaleDateString("en-US")}</TableCell>
                  <TableCell>{new Date(m.lastActive * 1000).toLocaleDateString("en-US")}</TableCell>
                  <TableCell><DonationsComponent donations={m.donationsHistory} /></TableCell>
                  <TableCell><WarsComponent wars={m.warHistory} /></TableCell>
                  <TableCell numeric>{m.donationsScore}</TableCell>
                  <TableCell numeric>{m.warScore}</TableCell>
                  <TableCell numeric>{m.totalScore}</TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </Paper>
    );
  }
}

export default withStyles(styles)(
  MembersComponent
);



