import {FETCH_MEMBERS} from "../action/members";

const initialState = {
  members: [],
};

export default function members(
  state = initialState,
  action
) {
  switch (action.type) {
    case FETCH_MEMBERS:
      return {
        ...state,
        members: action.members
      };
    default:
      return state;
  }
}


