import axios from "axios";

export const FETCH_MEMBERS = 'FETCH_MEMBERS';

export async function fetchMembers() {
  try {
    const response = await axios.get(
      "/data.json"
    );
    return {
      type: FETCH_MEMBERS,
      members: response.data.members,
      lastUpdated: response.data.lastUpdated,
    };
  } catch (err) {
    return {
      type: FETCH_MEMBERS,
      members: [],
    };
  }
}



